.icon {
  display: flex;
  align-items: center;
  height: 24px;
  width: 24px;
  background-size: cover;
  cursor: pointer;

  &--menu {
    background-image: url(../images/icons/icon-menu.svg);

    @include onDesktop {
      display: none;
    }
  }

  &--close {
    background-image: url(../images/icons/icon-close.svg);
  }

  &--facebook {
    background-image: url(../images/icons/icon-facebook.svg);

    &:hover {
      background-image: url(../images/icons/icon-facebook-hover.svg);
    }
  }

  &--instagram {
    background-image: url(../images/icons/icon-instagram.svg);

    &:hover {
      background-image: url(../images/icons/icon-instagram-hover.svg);
    }
  }

  &--twitter {
    background-image: url(../images/icons/icon-twitter.svg);

    &:hover {
      background-image: url(../images/icons/icon-twitter-hover.svg);
    }
  }
}
